import * as React from "react";
import styled from "styled-components";
import SystemInfo from "./SystemInfo";

import { colors } from "../../../../styled";

const HealthBar = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7px;
  border: 2px solid black;
  box-sizing: border-box;

  ${(props) => props.destroyed && "display: none;"}
  background-color: #7a2020;

  :before {
    content: "";
    position: absolute;
    width: ${(props) => `${props.health}%`};
    height: 100%;
    left: 0;
    bottom: 0;
    background-color: ${(props) =>
      props.criticals ? colors.hitpointsCritical : colors.hitpoints};
  }
`;

const SystemText = styled.div`
  position: absolute;
  top: 0px;
  width: 100%;
  height: calc(100% - 5px);
  font-family: arial;
  font-size: 10px;
  color: white;
  display: flex;
  ${(props) => props.destroyed && "display: none;"}
  align-items: flex-end;
  justify-content: center;
  text-shadow: black 0 0 6px, black 0 0 6px, black 0 0 6px, black 0 0 6px;
`;

const System = styled.div`
  position: relative;
  box-sizing: border-box;
  width: 30px;
  height: 30px;

  background-color: "transparent";
  background-image: ${(props) => `url(${props.background})`};
  background-size: cover;
  filter: ${(props) => {
    if (props.targeting) {
      return "hue-rotate(0deg) brightness(4) grayscale(0)";
    } else if (props.reserved) {
      return "hue-rotate(0deg) brightness(1) grayscale(0.7)";
    } else if (props.firing) {
      return "hue-rotate(0deg) brightness(4) grayscale(0)"; //"grayscale(100%) brightness(6) drop-shadow(0px 0px 5px white)";
    } else if (props.destroyed) {
      return "blur(1px) brightness(0.5)";
    } else {
      return "none";
    }
  }};
  cursor: pointer;

  ${SystemText} {
    display: ${(props) => (props.offline ? "none" : "flex")};
  }

  ${({ boosted }) =>
    boosted &&
    `
    :before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0.95;
      background-image: url(/img/boosted.png);
    `}

  :before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: ${(props) => {
      if (props.destroyed || props.offline || props.loading) {
        return "0.8";
      }

      return "0";
    }};

    background-color: transparent;

    background-image: ${(props) => {
      if (props.offline) {
        return "url(/img/offline.png)";
      }

      return "none";
    }};
  }
`;

const Overheat = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.95;
  background-image: url(/img/overheat.png);

  ${({ overheat }) =>
    overheat >= 1 &&
    `
    animation: blinker 1s linear infinite;

    @keyframes blinker {
      50% {
        opacity: 0.3;
      }
    }
  `}
`;

const Container = styled.div`
  cursor: pointer;
  position: relative;
  margin: 3px 0px;
  padding: 0 1px;
`;

class SystemIcon extends React.Component {
  constructor(props) {
    super(props);

    this.element = React.createRef();
    this.state = {
      mouseOveredSystem: null,
      clickedSystem: null,
    };
  }

  clickSystem(event) {
    event.stopPropagation();
    event.preventDefault();

    const { uiState, system, ship, scs } = this.props;

    uiState.customEvent("systemClicked", {
      ship,
      system,
      element: this.element,
      scs,
    });
  }

  onSystemMouseOver(event) {
    event.stopPropagation();
    event.preventDefault();

    let { uiState, system, ship, target } = this.props;

    const { weaponFireService } = uiState.services;
    const { gameData } = uiState;

    if (!target) {
      const targetId = weaponFireService.getSystemFireOrderTargetId(system);
      if (targetId) {
        target = gameData.ships.getShipById(targetId);
      }
    }

    uiState.customEvent("systemMouseOver", {
      ship,
      system,
      element: this.element,
      target,
    });

    this.setState({ mouseOveredSystem: this.element });
  }

  onSystemMouseOut(event) {
    event.stopPropagation();
    event.preventDefault();

    let { uiState } = this.props;

    uiState.customEvent("systemMouseOut");

    this.setState({ mouseOveredSystem: null });
  }

  onContextMenu(e) {
    e.stopPropagation();
    e.preventDefault();

    let { uiState, system, ship } = this.props;

    uiState.customEvent("systemRightClicked", { system, ship });
  }

  render() {
    let {
      uiState,
      system,
      ship,
      scs,
      destroyed,
      systemMenu: {
        systemInfoMenuProvider,
        activeSystem,
        activeSystemElement,
      } = {
        systemInfoMenuProvider: null,
        activeSystem: null,
        activeSystemElement: null,
      },
      onSystemClicked = this.clickSystem,
      selected = false,
      text = null,
      target = null,
      ...rest
    } = this.props;
    const { mouseOveredSystem } = this.state;
    const { weaponFireService } = uiState.services;

    if (!text) {
      text = system.getIconText();
    }

    const menu = activeSystem ? systemInfoMenuProvider : null;

    const displayMenu = Boolean(
      (!activeSystem && mouseOveredSystem) ||
        (activeSystem &&
          activeSystem === system &&
          activeSystemElement === this.element)
    );

    const firing = weaponFireService.systemHasFireOrder(system);
    const reserved =
      target &&
      weaponFireService.systemHasFireOrder(system) &&
      !weaponFireService.systemHasFireOrderAgainstShip(system, target);
    const targeting =
      target && weaponFireService.systemHasFireOrderAgainstShip(system, target);

    const disabled = system.isDisabled();
    const oldOverheat = system.heat.getOverheatPercentage();
    const { overheatPercentage: newOverheat } = system.heat.predictHeatChange();

    const overheat = newOverheat > oldOverheat ? newOverheat : oldOverheat;

    return (
      <>
        {displayMenu && scs && (
          <SystemInfo
            scs
            uiState={uiState}
            ship={ship}
            system={system}
            systemInfoMenuProvider={menu}
            element={mouseOveredSystem || activeSystemElement}
            target={target}
            {...rest}
          />
        )}
        <Container
          onClick={onSystemClicked.bind(this)}
          onMouseOver={this.onSystemMouseOver.bind(this)}
          onMouseOut={this.onSystemMouseOut.bind(this)}
          onContextMenu={this.onContextMenu.bind(this)}
          scs
        >
          {displayMenu && !scs && (
            <SystemInfo
              scs
              uiState={uiState}
              ship={ship}
              system={system}
              systemInfoMenuProvider={menu}
              element={mouseOveredSystem || activeSystemElement}
              target={target}
              {...rest}
            />
          )}
          <System
            ref={(c) => (this.element = c)}
            scs={scs}
            background={system.getBackgroundImage()}
            offline={isOffline(ship, system)}
            loading={isLoading(system)}
            selected={selected}
            firing={firing}
            targeting={targeting}
            reserved={reserved}
            destroyed={system.isDestroyed()}
            boosted={system.callHandler("getBoost", 0, false)}
          >
            {overheat > 0.25 && Boolean(!disabled) && (
              <Overheat overheat={overheat} />
            )}
          </System>

          <SystemText destroyed={system.isDestroyed()} selected={selected}>
            {text}
          </SystemText>
          <HealthBar
            destroyed={system.isDestroyed()}
            criticals={system.hasAnyCritical()}
            health={getStructureLeft(system)}
          />
        </Container>
      </>
    );
  }
}

const isLoading = (system) => undefined; //ASK FROM SYSTEM system.weapon && !weaponManager.isLoaded(system);

const isOffline = (ship, system) => system.power.isOffline();

const getStructureLeft = (system) =>
  ((system.hitpoints - system.getTotalDamage()) / system.hitpoints) * 100;

export default SystemIcon;
